* {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.hidden {
  display: none;
}

form {
  background-color: #6f2da8;
  justify-content: center;
  padding: 10px;
  display: flex;
  box-shadow: 0 3px 3px rgba(0, 0, 0, .15);
}

input {
  height: 30px;
  width: 300px;
  border: 2px solid rgba(0, 0, 0, .8);
  border-radius: 5px;
  padding: 2px 12px;
  font-size: 14px;
  box-shadow: 0 3px 3px rgba(0, 0, 0, .15);
}

button[type="submit"] {
  height: 37.2px;
  width: 37.2px;
  background-color: #fff;
  border-radius: 5px;
}

svg {
  height: 31px;
  width: 31px;
  border: 2px solid #fff;
  border-radius: 5px;
}

.search-box {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 1px;
  display: flex;
}

button[type="submit"]:focus, button[type="submit"]:hover {
  border-radius: 5px;
}

.photo-card {
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  box-shadow: 0 3px 3px #af69ee;
}

.photo {
  height: 240px;
  width: 400px;
  justify-content: center;
  align-items: center;
  display: flex;
  overflow: hidden;
}

.photo-card img {
  height: 370px;
}

.info {
  flex-direction: row;
  justify-content: center;
  gap: 20px;
  margin: 15px;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  gap: 7px;
  font-size: 15px;
  display: flex;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  margin: 30px;
  display: flex;
}

use {
  fill: #af69ee;
}

button.load-more {
  height: 45px;
  color: #fff;
  text-transform: uppercase;
  background-color: #af69ee;
  border: 2px solid #af69ee;
  border-radius: 5px;
  margin-bottom: 30px;
  padding: 0 20px;
  font-size: 14px;
  font-weight: 700;
  box-shadow: 0 3px 3px rgba(0, 0, 0, .15);
}

div.load-more-container {
  justify-content: center;
  display: flex;
}

button[type="submit"]:active use {
  fill: #9966cb;
}

button.load-more:active {
  background-color: #9966cb;
}

